import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { connect, useDispatch } from 'react-redux';
//import logo from '../../images/logo-full.png'
import Loader from '../pages/Loader/Loader';
import {
    loadingToggleAction,
    signupAction,
} from '../../store/actions/AuthActions';
function Register(props) {
    const [email, setEmail] = useState('');
    const [referralSourceCode, setReferralSourceCode] = useState('');
    const [firstname, setFirstname] = useState('');
    const [lastname, setLastname] = useState('');
    const [password, setPassword] = useState('');
    const [confPassword, setConfPassword] = useState('');
    let errorsObj = { firstname: '', lastname: '', email: '', referralSourceCode: '', password: '', confPassword: '' };
    const [errors, setErrors] = useState(errorsObj);

    const dispatch = useDispatch();

    function onSignUp(e) {
        e.preventDefault();
        let error = false;
        const errorObj = { ...errorsObj };
        if (firstname === '') {
            errorObj.firstname = 'Vorname ist erforderlich';
            error = true;
        }
		
		
		if (email === '') {
            errorObj.email = 'E-Mail ist erforderlich';
            error = true;
        }

        if (password === '') {
            errorObj.password = 'Passwort wird bentigt';
            error = true;
        }
		
		if (confPassword === '') {
            errorObj.confPassword = 'Passwort wird bentigt';
            error = true;
        }

        setErrors(errorObj);

        if (error) return;
        dispatch(loadingToggleAction(true));

        dispatch(signupAction(firstname, lastname, email, referralSourceCode, password, confPassword, props.history));
    }
  return (
   <div className="authincation h-100 p-meddle">
         <div className="container h-100">
            <div className="row justify-content-center h-100 align-items-center">
               <div className="col-md-6">
                  <div className="authincation-content">
                     <div className="row no-gutters">
                            <div className='col-xl-12'>
                                {props.showLoading && <Loader />}
                                <div className='auth-form'>
                                   <h4 className="text-center mb-4">Registrieren Sie sich</h4>
                                    {props.errorMessage && (
                                        <div className='bg-red-300 text-danger border border-red-900 p-1 my-2'>
                                            {props.errorMessage}
                                        </div>
                                    )}
                                    {props.successMessage && (
                                        <div className='bg-green-300 text-danger text-green-900  p-1 my-2'>
                                            {props.successMessage}
                                        </div>
                                    )}
                                    <form onSubmit={onSignUp}>
                                        <div className='form-group'>
                                            <label className='mb-1'>
                                              <strong>Vorname</strong>
                                            </label>
                                            <input type='text' className='form-control' 
												onChange={(e) => setFirstname(e.target.value)}
												placeholder='vorname' />
											{errors.firstname && <div className="text-danger fs-12">{errors.firstname}</div>}
                                        </div>
										<div className='form-group'>
                                            <label className='mb-1'>
                                              <strong>Nachname</strong>
                                            </label>
                                            <input type='text' className='form-control'
											onChange={(e) => setLastname(e.target.value)}
											placeholder='nachname' />
											
									   </div>
                                        <div className='form-group'>
                                            <label className='mb-1'>
                                              <strong>Email</strong>
                                            </label>
                                            <input type="email" className="form-control"
                                                value={email}
                                                onChange={(e) => setEmail(e.target.value)}
												placeholder='Email'
                                            />
                                            {errors.email && <div className="text-danger fs-12">{errors.email}</div>}
                                        </div>
										<div className='form-group'>
                                            <label className='mb-1'>
                                              <strong>Referral Code</strong>
                                            </label>
                                            <input type="text" className="form-control"
                                                value={referralSourceCode}
                                                onChange={(e) => setReferralSourceCode(e.target.value)}
												placeholder='enter your referral code'
                                            />
                                            {errors.referralSourceCode && <div className="text-danger fs-12">{errors.referralSourceCode}</div>}
                                        </div>
                                        <div className='form-group'>
                                            <label className='mb-1'>
                                              <strong>Passwort</strong>
                                            </label>
                                            <input type="password" className="form-control"
                                                value={password}
                                                onChange={(e) =>
                                                    setPassword(e.target.value)
                                                }
                                            />
                                        </div>
										<div className='form-group'>
                                            <label className='mb-1'>
                                              <strong>Passwort wiederholen</strong>
                                            </label>
                                            <input type="password" className="form-control"
                                                onChange={(e) =>
                                                    setConfPassword(e.target.value)
                                                }
                                            />
                                        </div>
                                        {errors.confPassword && <div className="text-danger fs-12">{errors.confPassword}</div>}
                                        <div className='text-center mt-4'>
                                            <input type='submit' value="Daten absenden" className='btn btn-primary btn-block'/>
                                        </div>
                                    </form>
                                    <div className='new-account mt-3 text-white'>
                                        <p>
                                            Already have an account?{' '}
                                            <Link className='text-primary' to='/login'>
                                                zurck
                                            </Link>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}

const mapStateToProps = (state) => {
    return {
        errorMessage: state.auth.errorMessage,
        successMessage: state.auth.successMessage,
        showLoading: state.auth.showLoading,
    };
};

export default connect(mapStateToProps)(Register);
