import axios from 'axios';
import swal from "sweetalert";
import {
    loginConfirmedAction,
    logout,
} from '../store/actions/AuthActions';

export function signUp(firstname, lastname, email, referralSourceCode, password, confPassword) {
    //axios call

    const postData = {
        firstname,
        lastname,
        email,
        referralSourceCode,
        password,
        confPassword,
        returnSecureToken: true,
    };

    return axios.post(
        'https://helpnexo.com:4000/users',
        postData,
    );
}

export function login(email, password) {
    const postData = {
        email,
        password,
        returnSecureToken: true,
    };

    return axios.post(
        'https://helpnexo.com:4000/login',
        postData,
    );
}

export function updateUser(userId, email, password, country, city, zip, age, occupation, lifegoals) {
    const postData = {
        userId, 
        email, 
		password, 
		country, 
		city, 
		zip, 
		age, 
		occupation, 
		lifegoals,
        returnSecureToken: true,
    };

    return axios.post(
        'https://helpnexo.com:4000/updateProfile',
        postData,
    );
}

export function formatError(errorResponse) {
    switch (errorResponse.error.message) {
        case 'EMAIL_EXISTS':
            //return 'Email already exists';
            swal("Oops", "Email already exists", "error");
            break;
        case 'EMAIL_NOT_FOUND':
            //return 'Email not found';
           swal("Oops", "Email not found", "error",{ button: "Try Again!",});
           break;
        case 'INVALID_PASSWORD':
            //return 'Invalid Password';
            swal("Oops", "Invalid Password", "error",{ button: "Try Again!",});
            break;
		case 'PASSWORD_NOT_MATCH':
            //return 'Not match Password';
            swal("Oops", "Password Not Match", "error",{ button: "Try Again!",});
            break;
		case 'WRONG_PASSWORD':
            //return 'Not match Password';
            swal("Oops", "Wrong Password", "error",{ button: "Try Again!",});
            break;
		case 'REFFERAL_NOT_FOUND':
            //return 'Not match Password';
            swal("Oops", "Refferal code not found", "error",{ button: "Try Again!",});
            break;	
		case 'MAIL_EXIST':
            //return 'Not match Password';
            swal("Oops", "Email Already Registered", "error",{ button: "Try Again!",});
            break;
		case 'UPLOAD_FAILED':
            //return 'Not match Password';
            swal("Oops", "Upload Fail : "+errorResponse.error.message, "error",{ button: "Try Again!",});
            break;
        case 'USER_DISABLED':
            return 'User Disabled';

        default:
            return '';
    }
}

export function saveTokenInLocalStorage(tokenDetails) {
    tokenDetails.expireDate = new Date(
        new Date().getTime() + tokenDetails.expiresIn * 1000,
    );
    localStorage.setItem('userDetails', JSON.stringify(tokenDetails));
    localStorage.setItem('userName', tokenDetails.displayName);
    localStorage.setItem('userId', tokenDetails.localId);
}

export function runLogoutTimer(dispatch, timer, history) {
    setTimeout(() => {
        dispatch(logout(history));
    }, timer);
}

export function checkAutoLogin(dispatch, history) {
    const tokenDetailsString = localStorage.getItem('userDetails');
    let tokenDetails = '';
    if (!tokenDetailsString) {
        dispatch(logout(history));
        return;
    }

    tokenDetails = JSON.parse(tokenDetailsString);
    let expireDate = new Date(tokenDetails.expireDate);
    let todaysDate = new Date();

    if (todaysDate > expireDate) {
        dispatch(logout(history));
        return;
    }
    dispatch(loginConfirmedAction(tokenDetails));

    const timer = expireDate.getTime() - todaysDate.getTime();
    runLogoutTimer(dispatch, timer, history);
}
