import React, { Fragment, useState, useEffect, useRef } from "react";
import swal from "sweetalert";
import profile from "../../../../images/profile/not_available_image.png"; 
import countries from "../../../../assets/countries.json"; 
import PageTitle from "../../../layouts/PageTitle";
import axios from "axios";


function AppProfile(props){
	
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [country, setCountry] = useState('Deutschland');
    const [city, setCity] = useState('');
    const [zip, setZip] = useState('');
    const [age, setAge] = useState('');
    const [occupation, setOccupation] = useState('');
    const [lifegoals, setLifegoals] = useState('');
	const [files, setFiles] = useState('');
	const [disUpload, setDisUpload] = useState(true);
	const [btnUpload, setBtnUpload] = useState('Hochladen');
	const [lblUpdateProfile, setLblUpdateProfile] = useState('Speichern');
    const inputRef = useRef(null);
    const formRef = useRef(null);
    const formUpdateRef = useRef(null);
    const [message, setMessage] = useState('');
    const [profilePic, setProfilePic] = useState(profile);
	const [base64, setBase64] = useState(null);
    const handleClick = () => inputRef && inputRef.current && inputRef.current.click();
    
	
	const createBase64Image = (file) => {
		const reader = new FileReader();
		return new Promise(function (resolve, reject) {
		  reader.onload = function (event) {
			resolve(event.target.result)
		  }
		  reader.readAsDataURL(file);
		})
	  }

	  const handleFiles = async (e) => {
		if (e.target.files[0]) {
		  const base64 = await createBase64Image(e.target.files[0]);
		  setBase64(base64);
		  setDisUpload(false);
		  setBtnUpload('Hochladen');
		  setFiles('to : '+e.target.files[0].name);
		}else{
			swal("Oops", "keine ausgewhlte Datei", "error");
		}
	  }

	  const handleSubmit = (e) => {
		e.preventDefault();
		setBtnUpload('Beladung...');
		const config = {
            headers: {
                'content-type': 'multipart/form-data'
            }
        };
		
		if (base64 && base64 !== '') {
		 const formData = new FormData();
		  formData.append('userId', localStorage.getItem('userId'));
		  formData.append('imageUpload', base64);
		  axios.post('https://helpnexo.com:4000/uploadPicture', formData, config)
			.then(data => setBtnUpload('Erfolg'))
			.catch((error) => setBtnUpload('Error'));
		  setBase64(null);
		  setFiles('');
		  setProfilePic(base64);
		  document.getElementById("picHeader").src = base64;
		  formRef.current && formRef.current.reset();
		  setTimeout(() => {
			setMessage('');
			setBtnUpload('Hochladen');
			setDisUpload(false);
		  }, 3000);
		}else{
			setDisUpload(true);
			setBtnUpload('Hochladen');
			swal("Oops", "keine ausgewhlte Datei", "error");
		}
	  }
	
	useEffect(() => {
        getUsersById();
		
    }, []);
 
 
    const getUsersById = async () => {
        const response = await axios.get('https://helpnexo.com:4000/users/'+localStorage.getItem('userId'));
		
		if(response.data.picture){
			setProfilePic(response.data.picture);
			document.getElementById("picHeader").src = response.data.picture;
		}
		
		setEmail(response.data.email);
		
		if(response.data.country){
			setCountry(response.data.country);
		}
		
        setCity(response.data.city);
        setZip(response.data.zip);
        setAge(response.data.age);
        setOccupation(response.data.occupation);
        setLifegoals(response.data.lifegoals);
    }
	
   

    const updateSubmit = (e) => {
        e.preventDefault();
		  setLblUpdateProfile('Beladung..');
		  axios.post('https://helpnexo.com:4000/updateProfile/'+localStorage.getItem('userId'), {email: email, password: password, country: country, city: city, zip: zip, age: age, occupation: occupation, lifegoals: lifegoals})
			.then(data => setLblUpdateProfile('Beladung..'))
			.catch((error) => swal("Oops", error.message, "error"));
		  
		  setTimeout(() => {
			swal("Erfolg", "Profil aktualisieren war erfolgreich", "success");
			setLblUpdateProfile('Update Profile');
		  }, 3000);
		
    }
   
   return (
      <Fragment>
         <PageTitle activeMenu="Profile" motherMenu="App" />

         
         <div className="row">
            
            <div className="col-xl-8">
               <div className="card">
                  <div className="card-body">
                     <div className="pt-3">
						<div className="settings-form">
						   <h4 className="text-primary">
							  Konto-Einstellung
						   </h4>
						   
						   <form ref={formRef}>
								<div className="form-row fade alert-dismissible fade show alert alert-success show">
								  <div className="form-group col-md-12">
									  <div className="form-group col-md-12" align="center">
									  <img
										  src={profilePic}
										  className="img-fluid rounded-circle"
										  width="200"
										  height="200"
									   />
									   {message}
									   <br/>
									   
									   <div role="alert" class="fade alert-dismissible fade show alert alert-success show" style={{ cursor: 'pointer' }} onClick={handleClick} >Photo whlen {files}</div>
									   </div>
									   <div className="form-group col-md-12" align="center">
										<input type="file" ref={inputRef} onChange={handleFiles} style={{ display: 'none' }} accept="image/*" />
										<button className="btn btn-primary" onClick={handleSubmit} disabled={disUpload}>{btnUpload}</button>
									   </div>
								   </div>
								</div>
							</form>
						   
						   <form ref={formUpdateRef}>
							   
							  <div className="form-row">
								 <div className="form-group col-md-6">
									<label>Email</label>
									<input
									   type="email"
									   placeholder="Email"
									   className="form-control"
									   value={email}
                                       onChange={(e) => setEmail(e.target.value)}
									/>
								 </div>
								 <div className="form-group col-md-6">
									<label>Passwort</label>
									<input
									   type="password"
									   placeholder="Passwort"
									   className="form-control"
									   value={password}
                                       onChange={(e) => setPassword(e.target.value)}
									/>
								 </div>
							  </div>
							  <div className="form-row">
								 <div className="form-group col-md-4">
									<label>Land</label>
									<select
									   className="form-control form-control-lg"
									   id="inputCountrty"
									   value={country}
									   defaultValue={country}
                                       onChange={(e) => setCountry(e.target.value)}
									>
									   <option value="">
										  Choose...
									   </option>
									   {Object.keys(countries).map((key, i) => (
										   <option value={countries[key]}>{countries[key]}</option>
									   ))}
									</select>
								 </div>
								 <div className="form-group col-md-6">
									<label>Stadt</label>
									<input
									   type="text"
									   className="form-control"
									   value={city}
                                       onChange={(e) => setCity(e.target.value)}
									/>
								 </div>
								 
								 <div className="form-group col-md-2">
									<label>PLZ</label>
									<input
									   type="text"
									   className="form-control"
									   value={zip}
                                       onChange={(e) => setZip(e.target.value)}
									/>
								 </div>
							  </div>
							  <div className="form-group">
								<div className="form-group col-md-2">
									<label>Alter</label>
									 <select
									   className="form-control"
									   id="inputAge"
									   value={age}
									   defaultValue={age}
                                       onChange={(e) => setAge(e.target.value)}
									 >
									 
									  {(() => {
										let rows = [];
										for (let i = 10; i < 111; i++) {
										  rows.push(<option key={i} value={i}>{i}</option>);
										}
										return rows;
									  })()}
									</select>
								 </div>
							  </div>
							  <div className="form-group">
								<div className="form-group col-md-6">
									<label>Beruf</label>
									<input
									   type="text"
									   className="form-control"
									   value={occupation}
                                       onChange={(e) => setOccupation(e.target.value)}
									/>
								 </div>
							  </div>
							  
							  <div className="form-group">
								<div className="form-group col-md-6">
									<label>Berufliche Ziele</label>
									<input
									   type="text"
									   className="form-control"
									   value={lifegoals}
                                       onChange={(e) => setLifegoals(e.target.value)}
									/>
								 </div>
							  </div>
							  
							  <button
								 className="btn btn-primary" 
								 onClick={updateSubmit}
							  >
								{lblUpdateProfile}
							  </button>
						   </form>
						</div>
					 </div>
                  </div>
               </div>
            </div>
         </div>
      </Fragment>
   );
};

export default AppProfile;
