import React, { Fragment, useState, useEffect} from "react";
import axios from "axios";

import PageTitle from "../../../layouts/PageTitle";
const Calendar = () => {
	
	const [lblMyPoints, setLblMyPoints] = useState('');
	const [lblMyReferralCode, setLblMyReferralCode] = useState('');
	
	useEffect(() => {
        getUsersById();
		
    }, []);
	
	const getUsersById = async () => {
        const response = await axios.get('https://helpnexo.com:4000/users/'+localStorage.getItem('userId'));
		setLblMyPoints(response.data.points);
		setLblMyReferralCode(response.data.my_referral_code);
		
    }
	
   return (
      <Fragment>
         <PageTitle activeMenu="Duties" motherMenu="App" />

         
         <div className="row">
            
            <div className="col-xl-12">
               <div className="card">
                  <div className="card-body">
					  <div className="col-xl-12">
						 <div className="read-content">
							<div className="media pt-3 d-sm-flex d-block justify-content-between">
								<div className="clearfix mb-3 d-flex">
									
									<div className="media-body mr-2">
										<h5 className="text-primary mb-0 mt-1">My Points</h5>
										<p className="mb-0">{lblMyPoints}</p>
									</div>
								</div>
								
							</div>
							<hr />
						 </div>
					 </div>
					 <div className="col-xl-12">
						 <div className="read-content">
							<div className="media pt-3 d-sm-flex d-block justify-content-between">
								<div className="clearfix mb-3 d-flex">
									
									<div className="media-body mr-2">
										<h5 className="text-primary mb-0 mt-1">My Referral Code</h5>
										<p className="mb-0">{lblMyReferralCode}</p>
									</div>
								</div>
								
							</div>
							<hr />
						 </div>
					 </div>
                  </div>
               </div>
            </div>
			
         </div>
      </Fragment>
   );
};

export default Calendar;
